	module.exports = {"data":{"post":{"html":"<p>The best lesson for surviving as an expat I learned my first quarter... in fact, my first weeks at the University of Minnesota.</p>\n<p>That lesson came early in the \"Introduction to Theater\" course taught by the legendary Arthur Ballet. My best friend Beth from high school was going to major in Theater, and even though I planned to major in International Relations, I had to take some Liberal Arts classes, so we took the class together. It was one of the most popular courses on campus, even though Prof. Ballet's health was poor and we often saw his lectures on video tape rather than live and in person.</p>\n<p>As a history of theater, we started with ancient Greece, and one of Prof. Ballet's favorite plays was <a href=\"http://www.sparknotes.com/drama/oedipus/summary.html\">Oedipus The King\"</a>, which he summed up in a single phrase: \"Don't be so damned sure!\" Don't ever assume that you're right, about anything! Anytime! Oedipus thought he was king of the world, but in fact he killed his father and married his mother. (In fact, even 30 years after I took the course, you can still find a Google result for <a href=\"http://www.google.com/search?rls=en&amp;q=%22don't+be+so+damned+sure%22+arthur+ballet&amp;ie=UTF-8&amp;oe=UTF-8\">Arthur Ballet \"don't be so damned sure\"</a>.)</p>\n<p>For an expat, this means you have to be ready for anything. Anything, and everything, can be different than what you expect. In fact, you have to question what you expect. A trivial example: do you want to bake <a href=\"http://www.verybestbaking.com/recipes/detail.aspx?id=18476\">chocolate chip cookies</a> in Germany? I'm sorry, you won't find chocolate chips in the grocery stores here. Or baking soda. Or brown sugar. Or even vanilla extract.</p>\n<p>Even if baking is not your thing, the concept remains... you have to be ready for anything and eveything to be different. And after a while, you'll notice that most of the differences are superficial, and the most important things that humans have in common are the same.</p>\n<p>To venture back to theater, another question raised by Professor Ballet is one he never answered in his introductory class. He mentioned a line that was the most profound in the history of theater, from <a href=\"http://en.wikipedia.org/wiki/The_Duchess_of_Malfi\">\"The Duchess of Malfi\"</a>, as the Duchess is killed by her brother, he says &ldquo;Cover her face; mine eyes dazzle; she died young.&rdquo; One night, on our way home after heavy drinking, Beth and I had the answer... we looked at each other and knew we had the meaning that Prof. Ballet had hinted at. But the next morning, it was gone. And today, if we were to meet and one of us would start to say \"Cover her face; mine eyes dazzle...\", we would both remember we once had the answer, but it is now gone.</p>","fields":{"title":"Don't Be So Damned Sure!","date":"02 Nov 2009","url":"https://papascott.de/2009-11-02-dont-be-so-damned-sure/"}}},"pathContext":{"slug":"/2009-11-02-dont-be-so-damned-sure/","prev":{"fields":{"title":"Marketing Non-Secrets 2","date":"22 Oct 2009","slug":"/2009-10-22-marketing-non-secrets-2/"}},"next":{"fields":{"title":"As The Wall Fell","date":"09 Nov 2009","slug":"/2009-11-09-as-the-wall-fell/"}}}}

